.dot-menu {
  width: 15px;
  height: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.dot {
  background: var(--black);
  width: 4px;
  height: 4px;
  border-radius: 50%;
  transition: all 0.6s cubic-bezier(0.66, -0.45, 0.35, 1.39);
}

.line2 {
  position: relative;
  bottom: 4px;
  transform: rotateZ(-45deg);
}

.line1 {
  position: relative;
  top: 3px;
  transform: rotateZ(45deg);
}

.dot-menu.active .dot1 {
  transform: translateY(12px);
}

.dot-menu.active .dot2 {
  transform: translateY(-12px);
}

.dot-menu.active .line1 {
  width: 20px;
  border-radius: 0px;
}

.dot-menu.active .line2 {
  width: 20px;
  transform: rotateZ(-45deg);
  border-radius: 0px;
}
